import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory , useParams} from "react-router-dom";
import styled, { useTheme } from 'styled-components'
import './Detail.scss';

let box = styled.div`
  padding : 20px;
`;

let 제목 = styled.h4`
 font-size : 25px;
 color : ${ props => props.색상}
 `;

 
//  //LifeCycle Hook
// class Detail2 extends React.Component {
//   componentDidMount(){
//     //Detail2 컴포넌트가 Mount 되고나서 실행할 코드
//   }
//   componentWillUnmount(){
//     //Detail2 컴포넌트가 Unmount 되기전에 실행할 코드
//   }
// }

function Detail(props){
  let [alert, alert변경] = useState(true);
  let [inputData ,inputData변경 ] = useState();
  useEffect(()=>{

    axios.get();
    let timer =  setTimeout(() => {
      
      alert변경(false);
      return () =>{ clearTimeout(timer);}
    }, 2000);
// console.log('hello');
    // return function 어쩌고(){  
    //   // 실행할코드  unmo
    // };
  // }, [alert] ); // 업데이트 될때만 ! 변경됨 ( 조건문)
  }, [] ); // 업데이트 될때만 ! 변경됨 ( 조건문)
  



    let history = useHistory();
    let { id} = useParams();

    let  test = props.item.findItme

    let findItme = props.item.find(function (item){

        return item.id == id
    })

    return(
      <div className="container">
        <box>

          {/* <제목 색상="blue"> 상세페이지</제목> */}
          <제목 className="red">상세페이지</제목>
          
        </box>

        {inputData}
        <input onChange={ (e)=>{ inputData변경(e.target.value) }}/>
        {
          alert === true 
          ? <AlertItemInfo></AlertItemInfo>
          : null
        }
        
            <div className="row">
              <div className="col-md-6">
                <img src= { '/' + id +'.jpg'} width="100%" />
                {/* <img src="/1.jpg" width="100%" /> */}
              </div>
              <div className="col-md-6 mt-4">
                <h4 className="pt-5">{ findItme.title }</h4>
                <p> {findItme.content}</p>
                <p>{ findItme.price} 원 </p>
                <Info 재고={props.재고 }></Info>
                <button className="btn btn-danger" onClick={( ) => {
                  
                  props.재고변경([9,10,11])
                }}>주문하기</button>
                <button className="btn btn-danger" onClick={()=>{ 
                    history.goBack();
                    // history.push('/')
                }}>뒤로가기</button>
              </div>
            </div>
          </div>
    )
  }


  function AlertItemInfo (){
    return (
      <div className="my-alert2">
        <p>재고가 얼마 남지 않았습니다</p>
      </div>
    )

  }


  function Info (props){
    return(
      <p> 재고 : {props.재고}</p>
    )
  }
  export default Detail;